@import url(https://fonts.googleapis.com/css?family=Roboto);
@import url(https://fonts.googleapis.com/css?family=Roboto:500);
@import url(https://fonts.googleapis.com/css?family=Roboto+Mono);
body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
    background: -webkit-linear-gradient(top left, #303030, #565557, #8A8A8C);
    background: linear-gradient(to bottom right, #303030, #565557, #8A8A8C);

  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

#root {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  height: 100vh;
  font-family: sans-serif;
}
#container {
   border: none;
   width: 23em;
   box-shadow: 5px 15px 20px -15px rgba(0, 0, 0, 0.3), 0 35px 50px -25px rgba(0, 0, 0, 0.3), 0 85px 60px -25px rgba(0, 0, 0, 0.1);
}
.buttons-container {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr 1fr;
  grid-template-rows: 1fr 1fr 1fr 1fr 1fr;
  height: 24em;
}
button {
    display: inline-block;
    border: 1.5px solid rgba(68,68,69,0.05);
    padding: 1rem 2rem;
    margin: 0;
    text-decoration: none;
    background: #F5FFFF;
    color: rgba(68,68,69,0.9);
    font-family: 'Roboto';
    font-size: 1.45em;
    font-weight: 500;
    cursor: pointer;
    text-align: center;
    -webkit-transition: background 250ms ease-in-out,
                -webkit-transform 150ms ease;
    transition: background 250ms ease-in-out,
                -webkit-transform 150ms ease;
    transition: background 250ms ease-in-out,
                transform 150ms ease;
    transition: background 250ms ease-in-out,
                transform 150ms ease,
                -webkit-transform 150ms ease;
    -webkit-appearance: none;
    -moz-appearance: none;
    outline: none;
}

button:hover {
    background: #B4F2FF;
}
button:active {
    -webkit-transform: scale(0.99);
            transform: scale(0.99);
}
#clear {
  grid-column: 1/2;
  color: #FF2E47;

}

#equals {
  grid-row: 4/6;
  grid-column: 4/5;
}
#zero {
  grid-column: 1/3;
}
.not-numbers{
  background: #EEF7F7;
}


#display-container {
  display: grid;
  grid-template-rows: 1fr 1fr;
  font-size: 2.1em;
  background: #F5FFFF;
  color: #444445;
  padding: .2em;
  font-family: 'Roboto';
  font-weight: 500;
  border-bottom: 1.5px solid rgba(68,68,69,0.09);

}
.display {
  text-align: right;
  padding-right: .6em;
  padding-top: 0.05em;
  color: rgba(68,68,69,0.4);
  overflow: auto;
}
#display {
  grid-row: 2/3;
  color: rgba(68,68,69,0.85);
}

